.home-page
{
    width: 100%;
    height: auto;
    /* background-color: rgb(36, 7, 77); */
    color: white;
    letter-spacing: normal;
    background-image: linear-gradient(to right, #0e0b28, #302b63, #24243e);
    
    
    
}
.home-container
{
    margin-top: 10%;
    margin-bottom: 5%;
}
.image
{
   height:auto;
   width: 50%;
   padding: 10px;
   border-radius:50%;
    
   /* float:right; */
   border-right: 8px solid orangered;
   border-bottom: 8px solid orangered;
   box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
   /* padding: auto; */
}
.btns
{
    display: flex;
    margin-left: 3%;
    margin-bottom: 3%;
   justify-content: space-between;


}
#btn1,#btn2
{
    border-radius: 30px;
    height: 2rem;
    width: 10rem;
    color: aliceblue;

}
#btn2
{

 background-color: orangered;
 justify-content: center;

 border:none;

}
#btn1
{
    color: white;
    border:none;
    background-color: rgb(36, 7, 77);
    border: 2px solid white;
   

}
.word {
    margin: auto;
    color: white;
    font: 700 normal 2em 'tahoma';
    text-shadow: 5px 2px #222324, 2px 4px #222324, 3px 5px #222324;
    position: relative;
    height: 50px;
  }
